import React from 'react';
import './Download.css';


class Download extends React.Component {

  create_png(img, scale) {
    let ctx = this.create_canvas(img)

    let width = img['width']
    let height = img['height']

    const step_w = Math.round(width / 24)
    const step_h = Math.round(height / 24)
    let arr = [...Array(24)].map(e => Array(24));

    let pos_h = Math.round(height / 24 / 2)
    for (let y = 0; y < 24; y++) {
        let pos_w = Math.round(width / 24 / 2)
        for (let x = 0; x < 24; x++) {
            arr[x][y] = this.get_rgba(ctx, pos_w, pos_h)
            pos_w += step_w
        }
        pos_h += step_h
    }

    let canvas = window.document.createElement('canvas');
    canvas.width = 24 * scale
    canvas.height = 24 * scale
    let ctx_tgt = canvas.getContext("2d");

    for (let y = 0; y < 24; y++) {
      for (let x = 0; x < 24; x++) {
        // let color = arr[x][y]
        // let r = color[0]
        // let g = color[1]
        // let b = color[2]
        // let a = color[3] / 255
        // ctx_tgt.fillStyle = "rgba(" + r + "," + g + "," + b + "," + a + ")";
        ctx_tgt.fillStyle = arr[x][y]
        ctx_tgt.fillRect(x*scale, y*scale, scale, scale);
      }
    }

    let link = document.createElement('a');
    link.download = 'cyberdoggo.png';
    link.href = canvas.toDataURL()
    link.click();
  }

  create_svg(img) {
    let ctx = this.create_canvas(img)

    let svg = document.createElementNS("http://www.w3.org/2000/svg", "svg")
    svg.width = 24
    svg.height = 24
    function getNode(x, y, c) {
      let e = document.createElementNS("http://www.w3.org/2000/svg", "rect");
      e.setAttributeNS(null, "width", "1");
      e.setAttributeNS(null, "height", "1");
      e.setAttributeNS(null, "x", x);
      e.setAttributeNS(null, "y", y);
      e.setAttributeNS(null, "fill", c);
      svg.append(e)
    }

    let width = img['width']
    let height = img['height']

    const step_w = Math.round(width / 24)
    const step_h = Math.round(height / 24)

    let pos_h = Math.round(height / 24 / 2)
    for (let y = 0; y < 24; y++) {
      let pos_w = Math.round(width / 24 / 2)
      for (let x = 0; x < 24; x++) {
        let color = this.get_rgba(ctx, pos_w, pos_h)
        getNode(x, y, color)
        pos_w += step_w
      }
      pos_h += step_h
    }

    var data = (new XMLSerializer()).serializeToString(svg);
    var DOMURL = window.URL || window.webkitURL || window;
    var svgBlob = new Blob([data], {type: 'image/svg+xml;charset=utf-8'});
    var link = document.createElement('a');
    link.download = 'cyberdoggo.svg';
    link.href = DOMURL.createObjectURL(svgBlob)
    link.click()
  }

  create_canvas(img) {
    const canvas = window.document.createElement('canvas');
    let width = console.log(img['width'])
    let height = console.log(img['height'])
    const ctx = canvas.getContext('2d');
    ctx.width = width
    ctx.height = height
    ctx.drawImage(img, 0, 0)
    return ctx
  }

  get_rgba(ctx, x, y) {
    let c = ctx.getImageData(x, y, 1, 1)
    let r = c.data[0]
    let g = c.data[1]
    let b = c.data[2]
    let a = c.data[3]
    return "#" + ((r << 24) + (g << 16) + (b << 8) + a).toString(16).padStart(8, '0');
  }

  render() {
    return (
      <div>
        <div>SLIDER</div>
        <div>
          <div className="button" onClick={() => this.create_png(this.props.img, 30)}>Download PNG</div>
          <div className="button" onClick={() => this.create_svg(this.props.img)}>Download SVG</div>
          <div className="button cancel" onClick={() => this.create_svg(this.props.img)}>Cancel</div>
        </div>
      </div>
    )
  }
}

export default Download
