import dia_legendary from "./imgs/diamonds/legendary.svg";
import dia_ultra from "./imgs/diamonds/ultra.svg";
import dia_rare from "./imgs/diamonds/rare.svg";
import dia_uncommon from "./imgs/diamonds/uncommon.svg";
import dia_common from "./imgs/diamonds/common.svg";
import floating from "./imgs/floating.png";
import Card from "./Card";
import React from "react";
import rarities from "./res/rarities.json";


export default class Home extends React.Component {

  state = {
    search_txt: '',
    hide_sold: false,
    rarity: 'all',
    sort_by_rarity: false
  }

  constructor(props) {
    super(props);
    this.search = ''
    this.doggos = rarities
  }

  onSearchChange(event) {
    this.setState({
      search_txt: event.target.value
    })
    this.forceUpdate()
  }

  onSoldChange(event) {
    this.setState({
      hide_sold: event.target.checked
    })
  }

  onRarityChange(event) {
    this.setState({
      rarity: parseInt(event.target.value)
    })
  }

  onOrderChange(event) {
    this.setState({
      sort_by_rarity: event.target.value === 'true'
    })
  }

  get_doggos() {
    let result = this.doggos
    if (this.state.hide_sold)
      result = result.filter(dog => !dog.sold)
    // console.log(this.state.rarity)
    if (this.state.rarity > -1)
      result = result.filter(dog => dog.rarity === this.state.rarity)
    result = result.filter(dog => dog.name.includes(this.state.search_txt))
    if (this.state.sort_by_rarity)
      result = result.sort((a, b) => b.score - a.score)
    // console.log(result.length)
    return result
  }

  render() {
    return (
      <div>
        <div className="section">--- About ---</div>
        <p>
          One thousand CyberDoggos are floating around on the open sea waiting for your help. Rescue them by providing a
          safe place on the Ethereum blockchain where they can live forever.
        </p>

        <div className="center">
          <img src={floating} alt="Floating Doggo" width="160px" />
        </div>

        <div className="center">
          <a className="opensea" href="https://opensea.io/collection/cyber-doggos">Get Your Doggo</a>
        </div>

        <p>
          The CyberDoggo collection consists of one thousand mostly programmatically generated doggos. Each doggo is
          unique and differs from every other doggo in at least two traits. All remaining doggos (except the LEGENDARY
           ones) find their way onto the blockchain for 0.03 ETH.
        </p>

        <div className="section">--- Rarity Levels ---</div>
        <p>
          All CyberDoggos are classified into five different rarity levels. A rarity score is calculated for each
          doggo
          and the doggos are assigned to the different rarity levels according to this score.
        </p>

        <div className="center">
          <div className="diamonds">
            <img className="diamond" src={dia_legendary} alt="Legendary"/>
            <div className="flex-fill"/>
            <img className="diamond" src={dia_ultra} alt="Utra Rare"/>
            <div className="flex-fill"/>
            <img className="diamond" src={dia_rare} alt="Rare"/>
            <div className="flex-fill"/>
            <img className="diamond" src={dia_uncommon} alt="Uncommon"/>
            <div className="flex-fill"/>
            <img className="diamond" src={dia_common} alt="Common"/>
          </div>
        </div>

        <p>
          The 10 <span className="legendary">LEGENDARY</span> doggos are unique, manually created special editions
          with
          a rarity score of 100. For each of the remaining 990 doggos the rarity score is determined by the
          probabilities of its traits. The top 49 doggos are considered to be <span
            className="ultra-rare">ULTRA RARE</span>,
          the next 202 with a score of 90 and above <span className="rare">RARE</span> and the following 286 <span
            className="uncommon">UNCOMMON</span>.
          The remaining doggos with scores of below 80 are <span className="common">COMMON</span>.
        </p>

        <div className="section">--- Find your Doggo ---</div>
        <p>
          Search for a specific doggo or filter by the rarity level. Note that the information if a dog
          is already sold is not requested dynamically and might be outdated.
        </p>

        <div className="search">
          <input type="text" id="fname" name="fname" placeholder="e.g. #420"
                 style={{minWidth: "100px", flex: "1 0 100px"}} onChange={this.onSearchChange.bind(this)}/>

          <select name="rarity" id="rarity" onChange={this.onRarityChange.bind(this)}>
            <option value="-1">All Rarities</option>
            <option value="4">Legendary</option>
            <option value="3">Ultra Rare</option>
            <option value="2">Rare</option>
            <option value="1">Uncommon</option>
            <option value="0">Common</option>
          </select>

          <select name="sort_by_rarity" id="sort_by_rarity" onChange={this.onOrderChange.bind(this)}>
            <option value="false">Sort by Name</option>
            <option value="true">Sort by Rarity</option>
          </select>

          <div className="flex-fill" />

          <div style={{whiteSpace: "nowrap"}}>
            <input type="checkbox" id="hide-sold" name="hide-sold" onChange={this.onSoldChange.bind(this)}/>
            <label htmlFor="hide-sold">Hide Sold Items</label>
          </div>
        </div>

        <div style={{display:"inline"}}>
          {this.get_doggos().map(function (object, i) {
            return (<Card key={i} item={object} />);
          })}
        </div>
      </div>
    )
  }
}